package style

import content.visibility
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.css.keywords.auto
import style.AppStylesheet

object ProjectsStyle : StyleSheet(AppStylesheet) {


    val openSourceStyle by style {
        position(Position.Fixed)
        overflow("hidden")
        marginTop(0.px)
        width(auto)
        height(auto)
        property("bottom","0")
        property("right","0")
        display(DisplayStyle.Flex)
        alignItems(AlignItems.FlexEnd)
        justifyContent(JustifyContent.FlexEnd)
        paddingRight(30.px)
        property("z-index", "10")
        media(mediaMaxWidth(420.px)){
            self style {
                visibility("hidden")
            }
        }
    }

    val projectStyleItem by style {
        display(DisplayStyle.Flex)
        height(auto)
        flexBasis(100.percent)
        marginTop(5.px)
        flexDirection( FlexDirection.Column )
        media(mediaMaxWidth(420.px)){
            self style {
                flexDirection( FlexDirection.Column)
                justifyContent(JustifyContent.Center)
                alignItems(AlignItems.Center)
            }
        }
    }

    val projectsMain by style {

        flexDirection(FlexDirection.Column)
        display(DisplayStyle.Flex)
        paddingLeft(20.px)
        paddingRight(20.px)
        paddingTop(10.px)
        paddingBottom(20.px)
        height(auto)
        width(auto)
        justifyContent(JustifyContent.Center)
        alignItems(AlignItems.Center)
        textAlign("center")
    }

    val projectsSection by style {
        height(auto)
        backgroundColor(projectMainBg)
        backgroundSize("cover")
    }

    val projectsColumn1 by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        justifyContent(JustifyContent.FlexStart)
        paddingLeft(30.px)
        marginLeft(10.px)
        marginRight(10.px)
        paddingRight(30.px)
        paddingTop(30.px)
        paddingBottom(30.px)
        height(auto)
        width(300.px)
    }

    val skillProficientText by style {
        display(DisplayStyle.Flex)
        justifyContent(JustifyContent.FlexStart)
        fontWeight(300)
        marginTop(20.px)
        property("font-family", "Raleway, sans-serif")
        property("font-size", "large")
    }


    val projectsHeaders by style {
        textAlign("center")
        fontWeight(700)
        margin(15.px)
        property("font-family", "Open Sans, sans-serif")
        property("color", onWhiteColor)
        property("font-size", "xx-large")

    }

    val PColumn1 by style {
        display(DisplayStyle.Flex)
        justifyContent(JustifyContent.Center)
        flexBasis(50.percent)
        margin(10.px)
        whiteSpace("nowrap")
        overflow("auto")
        width(100.percent)
        flexDirection(FlexDirection.Row)
        height(100.percent)
        backgroundColor(rgba(0, 0, 0, 0.06))
        borderRadius(10.px)
        alignItems(AlignItems.Center)
        textAlign("center")
        media(mediaMaxWidth(420.px)){
            self style {
                width(300.px)
            }
        }
    }

    val Image by style {
        width(240.px)
        height(480.px)
        marginLeft(20.px)
        marginRight(20.px)
        marginTop(40.px)
        marginBottom(40.px)
        borderRadius(15.px)
        media(mediaMaxWidth(420.px)){
            self style {
                marginLeft(10.px)
                marginRight(10.px)
                marginTop(10.px)
                marginBottom(10.px)
                borderRadius(10.px)
            }
        }
    }


    val PColumnText by style {
        flexBasis(50.percent)
        margin(8.px)
        flexDirection(FlexDirection.Column)
        height(300.px)
        display(DisplayStyle.Flex)
        justifyContent(JustifyContent.FlexStart)
        textAlign("start")
    }

    val projectGitLink by style {
        display(DisplayStyle.Flex)
        justifyContent(JustifyContent.FlexStart)
        fontWeight(500)
        marginTop(10.px)
        property("font-family", "Raleway, sans-serif")
        property("font-size", "large")
    }

    val projectImageBg by style {
        backgroundColor(Color("white"))
        width(400.px)
        overflow("auto")
        height(300.px)
        display(DisplayStyle.Flex)
        justifyContent(JustifyContent.FlexStart)
        alignItems(AlignItems.Center)
    }


}
