package content

import androidx.compose.runtime.Composable
import org.jetbrains.compose.web.attributes.ATarget
import org.jetbrains.compose.web.attributes.target
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.css.keywords.auto
import org.jetbrains.compose.web.dom.*
import style.MainStyle.itemNormalHeaderTexts
import style.MainStyle.normalTexts
import style.ProjectsStyle.Image
import style.ProjectsStyle.PColumn1
import style.ProjectsStyle.PColumnText
import style.ProjectsStyle.projectGitLink
import style.ProjectsStyle.projectImageBg
import style.ProjectsStyle.projectStyleItem
import style.ProjectsStyle.projectsHeaders
import style.ProjectsStyle.projectsMain
import style.ProjectsStyle.projectsSection
import style.SkillsStyle
import style.SkillsStyle.skillsSection
import style.mainColor
import style.onWhiteColor

@Composable
fun Projects() {
    Section(
        attrs = {
            classes(projectsSection)
            style {
                position(Position.Relative)
            }
        }
    ) {

        Div(attrs = {
            classes(projectsMain)
        })
        {


            P(attrs = {
                classes(projectsHeaders)
            }) {
                Text("Open Source Projects and Articles")
            }

            val openSourceProjects = listOf(
                kmmChatGPT,
                composeNews,
                flCurrencyConvertor,
                therapeutic,
                Project(
                    name = "Savit Authenticator",
                    description = "Savit Authenticator is an open source  Authenticator App built with Jetpack Compose deployed using Jenkins CI, built to Encourage the use Offline Time / Counter Based One Time Passwords. (This project made some references to the Open Source Google Authenticator project)",
                    githubLink = "https://github.com/develNerd/SavitAuthenticator",
                    articleLink = "",
                    playstoreLink = "",
                    image = listOf(

                        resolveImageUrl("auth_1.jpg"),
                        resolveImageUrl("auth_2.jpg"),
                        resolveImageUrl("auth_3.jpg"),
                        resolveImageUrl("auth_4.jpg"),
                        resolveImageUrl("auth_5.jpg"),
                        resolveImageUrl("auth_6.jpg"),
                        resolveImageUrl("auth_7.jpg"),
                        resolveImageUrl("auth_8.jpg"),

                        )
                ),
                Project(
                    name = "Space Jam",
                    description = "SpaceJam is a Jetpack Compose app, built on top of clean-architecture and is featuring graphql, sqldelight, canvas api/custom UIs, navigation, material3 and many more :). \n" +
                            "SpaceJam uses SpaceX's open sourced api written with GraphQl. Enjoy all Spacex's past launches, ships, launches, next launches and many more with spacejam. NB : Still in dev.",
                    githubLink = "https://github.com/develNerd/SpaceJam",
                    articleLink = "",
                    playstoreLink = "",
                    documentationLink = "https://develnerd.github.io/SpaceJam/",
                    image = listOf(
                        resolveImageUrl("spacejam_1.jpg"),
                        resolveImageUrl("space_jam_2.jpg"),
                        resolveImageUrl("space_jam_3.jpg"),
                        resolveImageUrl("space_jam_4.jpg")
                    )
                ),
                Project(
                    name = "JC-Switch",
                    description = "JC-Switch is Jetpack Compose Custom Switch Library built to enable fellow android developers implement a more customisable switch aside the orthodox android switch \n" +
                            "provided by android or the material design libraries. Also learn how to write some compose test cases using the test source code. It is easy to use and has a pretty straight forward documentation. Fill free to use it in any project and fork or make a pull request if you wish to contribute.",
                    githubLink = "https://github.com/develNerd/JC-CustomSwitch",
                    articleLink = "",
                    playstoreLink = "",
                    image = listOf("jc_switch.gif")
                ),

                Project(
                    name = "JC-PortolioSite",
                    description = "JC-PortolioSite is a static website template built jetpack compose (Compose for Web) to help fellow developers extend their compose abilities to web development. Feel free to use this template for your portfolio site",
                    githubLink = "https://github.com/develNerd/JC-PortfolioSite",
                    articleLink = "",
                    playstoreLink = "",
                    image = listOf("ic_compose_web.svg")
                ),

                Project(
                    name = "Android 12 Animation Clone",
                    description = "This project is a clone of the Android 12 Clock Animation in the settings app." +
                            " It helps provide in depth knowledge about  Android's Touch Input Geometry in General." +
                            "You learn how to implement Rotation related stuff using Jetpack compose with touch events" +
                            "" +
                            "",
                    githubLink = "https://github.com/develNerd/AnimatingCompoables",
                    articleLink = "",
                    playstoreLink = "",
                    image = listOf("clock_image.gif")
                ),
                Project(
                    name = "Animating Composables",
                    description = "A Beginners Tutorial for Animating Jetpack Compose Views. The article focuses on helping beginners understand the basics of animations in compose and also how it could be effectively used to enrich user interfaces wjen building android apps.\n",
                    githubLink = "https://github.com/develNerd/AnimatingCompoables",
                    articleLink = "https://dev.to/devnerd/animating-composables-made-easy-4i7f",
                    playstoreLink = ""
                ),
                Project(
                    name = "Blind 75 in Kotlin",
                    description = "Let's solve the hand picked 75 questions on LeetCode for white board interviews in Kotlin :). This is to help prepare us when ever we are going for coding interviews pertaining to Data Structures and Algorithms\n NB: Still Ongoing",
                    githubLink = "https://github.com/develNerd/Blind75InKotlin",
                    articleLink = "",
                    playstoreLink = "",
                    image = listOf("leet_code_log.svg")
                ),
                Project(
                    name = "Sectioned Progress Bar for Compose",
                    description = "A Sectioned Progress Indicator built in Jetpack Compose. Easily specify number of sections with corresponding section colors",
                    githubLink = "https://github.com/develNerd/JCSectionedProgressIndicator",
                    articleLink = "",
                    playstoreLink = "",
                    image = listOf("section_progress_bar.gif")
                ),
                Project(
                    name = "Savit Business (Featured)",
                    description = "Savit Business is an app suitable for businesses to track their Finance In the most simplest way. The app promises efficient features to help run a successful business.",
                    githubLink = "",
                    articleLink = "",
                    playstoreLink = "https://play.google.com/store/apps/details?id=org.savit.savitbusinessapp",
                    image = listOf(
                        resolveImageUrl("savit_1.jpg"),
                        resolveImageUrl("savit_2.jpg"),
                        resolveImageUrl("savit_3.jpg"),
                        resolveImageUrl("savit_4.jpg"),
                        resolveImageUrl("savit_5.jpg"),
                        resolveImageUrl("savit_6.jpg"),
                    )
                ),

                )

            openSourceProjects.forEachIndexed { index, project ->
                Div(
                    attrs = {
                        classes(projectStyleItem)

                    }
                ) {

                    Div(
                        attrs = {
                            classes(PColumnText)
                        }
                    ) {

                        P(attrs = {
                            classes(itemNormalHeaderTexts)
                            style {
                                property("color", onWhiteColor)
                            }
                        }) {
                            Text(project.name)
                        }

                        P(attrs = {
                            classes(normalTexts)
                            style {
                                property("color", onWhiteColor)
                            }
                        }) {
                            project.description.split("*").forEach {
                                P {
                                    Text(it)
                                }
                            }

                        }


                        if (project.githubLink.isNotEmpty()) {
                            A(href = project.githubLink, attrs = {
                                classes(projectGitLink)
                                target(ATarget.Blank)
                            }) {
                                Text("Github Link")
                            }
                        }

                        if (project.playstoreLink.isNotEmpty()) {
                            A(href = project.playstoreLink, attrs = {
                                classes(projectGitLink)
                                target(ATarget.Blank)
                            }) {
                                Text("PlayStore Link")
                            }
                        }

                        if (project.apk.isNotEmpty()) {
                            A(href = project.apk, attrs = {
                                classes(projectGitLink)
                                target(ATarget.Blank)
                            }) {
                                Text("APK Link")
                            }
                        }

                        if (project.documentationLink.isNotEmpty()) {
                            A(href = project.documentationLink, attrs = {
                                classes(projectGitLink)
                                target(ATarget.Blank)
                            }) {
                                Text("Documentation")
                            }
                        }


                        if (project.videoLink.isNotEmpty()) {
                            A(href = project.videoLink, attrs = {
                                classes(projectGitLink)
                                target(ATarget.Blank)
                            }) {
                                Text("Video Link")
                            }
                        }

                        if (project.articleLink.isNotEmpty()) {
                            A(href = project.articleLink, attrs = {
                                classes(projectGitLink)
                                target(ATarget.Blank)
                            }) {
                                Text("Article Link")
                            }
                        }

                    }
                    Div(
                        attrs = {
                            classes(PColumn1)
                        }
                    ) {
                        val itemSize = project.image
                        Div(attrs = {

                            style {
                                display(DisplayStyle.Flex)
                                backgroundColor(Color("white"))
                                borderRadius(10.px)
                                width(700.px)
                                if (itemSize.size > 1) {
                                    justifyContent(JustifyContent.FlexStart)
                                    height(auto)
                                } else {
                                    justifyContent(JustifyContent.Center)
                                    height(400.px)
                                }
                                overflow("auto")
                                alignItems(AlignItems.Center)
                            }
                        }) {
                            if (project.image.size > 1) {
                                project.image.forEach { image ->
                                    Img(src = image.ifEmpty { "ic_github.svg" }, attrs = {
                                        classes(Image)
                                    })

                                }

                            } else {
                                project.image[0].apply {
                                    Img(src = this.ifEmpty { "ic_github.svg" }, attrs = {
                                        style {
                                            width(100.px)
                                            height(100.px)
                                            marginLeft(10.px)
                                            marginRight(10.px)
                                        }
                                    })
                                }
                            }

                        }
                    }


                }
            }


        }

    }
}

data class Project(
    val name: String,
    val description: String,
    val githubLink: String,
    val articleLink: String = "",
    val playstoreLink: String = "",
    val documentationLink: String = "",
    val videoLink: String = "",
    val image: List<String> = listOf("ic_github.svg"),
    val apk:String = ""
)

